@mixin cs-scroll {
	@include scroll;
	@include scrollbar {
		width: 4px; }
	@include scrollbar-track {
		width: 4px; }
	@include scrollbar-thumb {
		background-color: #c4c4c4; } }

@mixin bdrs {
	border-radius: $border-radius; }

@mixin bd {
	border: 1px solid $color-border--lighten;
	border-radius: $border-radius; }

@mixin flex {
	display: flex; }

@mixin flexCenter {
	display: flex;
	align-items: center; }

@mixin flexColumn {
	display: flex;
	flex-direction: column; }

%textLight {
	font-family: $f-light;
	font-weight: 300; }

%inputStyle {
	display: inline-block;
	background-color: #fff;
	border: 1px solid $gray--light;
	border-radius: $border-radius;

	outline: none;
	appearance: none;
	-webkit-appearance: none;
	box-shadow: none {
    background-color: transparent; }

	&:-webkit-autofill {
		-webkit-box-shadow: 0 0 0 30px white inset; }

	@include placeholder {
		color: $gray--light;
		font-size: 14px; }

	&:hover {
		border-color: #a0a0a0; }

	&:focus {
		border-color: $color-border; } }

%responsMobile {
	@include r(480) {
		max-width: 425px;
		width: 100%; }
	@include r(425) {
		max-width: 414px; }
	@include r(414) {
		max-width: 375px; }
	@include r(375) {
		max-width: 355px; }
	@include r(320) {
		max-width: 300px; } }

%shadow {
	box-shadow: 0 2px 4px 0 rgba(0,0,0,.1), 0 0 4px 0 rgba(0,0,0,.1); }

%box {
	background: #fff;
	border-radius: $border-radius;
	box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.08); }

%boxItem {
	background: #fff;
	border: 0.25px solid #C4C4C4;
	border-radius: $border-radius;
	box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.08); }
