.page-home {
	.firstscreen {
		min-height: 500px;
		margin-bottom: 60px;

		@include r-max(sm) {
			min-height: auto;
			margin-bottom: 40px; }

		@include r-max(xs) {
			margin-bottom: 30px; } } }

//.login
.login {

	//.login__icon
	&__icon {
		padding: 5px;
		width: 23px;
		height: 23px;
		@include centre('y');
		right: 20px;
		fill: $color-text--light;
		@include tr; }

	//.login__clear
	&__clear {}

	//.login__auth
	&__auth {
		display: none; } }
//.login end

//.home-search
.home-search {
	@include r(768) {
		margin-bottom: 30px; }

	//.home-search__icon
	&__in {
		@include flex;
		justify-content: center;
		@include r(768) {
			flex-wrap: wrap; } }

	//.home-search__icon
	&__icon {
		width: 27px;
		height: 27px;
		margin-right: 15px;
		fill: $gray--light;
		@include tr;
		@include r(768) {
			fill: $color-secondary; }
		&--series {
			width: 22px;
			height: 22px; }
		&--articul {
			width: 24px;
			height: 24px; } }

	//.home-search__form
	&__form {
		@include flexCenter;
		@include r(480) {
			display: block; } }

	//.home-search__block
	&__block {
		flex: 1;
		@include flexCenter;
		@include r(480) {
			margin-bottom: 15px;
			flex-direction: column; }
		.select2-container {
			width: 50% !important;
			margin-right: 15px;
			@include r(480) {
				width: 100% !important;
				margin-right: 0;
				margin-bottom: 15px;
				&:last-child {
					margin-bottom: 0; } } } }

	//.home-search__btn
	&__btn {
		text-transform: uppercase;
		letter-spacing: 0.7px;
		@include r(768) {
			display: block;
			margin: auto; }
		@include r(480) {
			width: 100%; } }

	//.home-search__list
	&__list {
		width: 20%;
		margin: 0 auto;
		@include r(768) {
			width: 50%; }
		@include r(480) {
			width: 100%; }
		&:first-child {
			margin-left: 0; }
		&:last-child {
			margin-right: 0; }
		&--wide {
			width: 100% !important;
			@include flexCenter;
			@include r(320) {
				flex-direction: column;
				align-items: flex-start; }
			.home-search__item {
				& + .home-search__item {
					margin-left: 15px;
					@include r(320) {
						margin-left: 0; } } }
			.home-search__link {
				margin-bottom: 0; } } }

	//.home-search__link
	&__link {
		display: inline-block;
		margin-bottom: 10px;
		padding: 3px 8px;
		font-size: 14px;
		color: #000;
		transition: initial;
		position: relative;
		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 1px;
			border-bottom: 1px dashed #000; }
		&:hover {
			color: #fff;
			background-color: $color-accent;
			border-bottom-color: transparent;
			border-radius: $border-radius;
			&::after {
				border-color: transparent; } } }

	//.home-search__search
	&__search {
		height: 38px;
		line-height: 38px;
		margin-right: 15px;
		padding: 0 30px 0 15px;
		border: 1px solid $color-border;
		border-radius: $border-radius;
		position: relative;
		flex: 1;
		@include r(480) {
			width: 100%;
			margin-right: 0;
			margin-bottom: 15px; }
		input {
			width: 100%;
			color: #000;
			border: 0; } }

	//.home-search__clear
	&__clear {
		width: 30px;
		font-family: $f;
		font-size: 14px;
		text-align: center;
		font-style: normal;
		cursor: pointer;
		@include vy;
		right: 0;
		&:hover {
			color: #000; } }

	//.home-search .select
	.select2-container .select2-selection--single {
		height: 38px; }
	.select2-container--default .select2-selection--single .select2-selection__rendered {
		line-height: 38px;
		padding-right: 30px; }
	.select2-container--default .select2-selection--single .select2-selection__arrow {
		width: 25px;
		top: 7px; }
	.select2-dropdown {
		max-height: 100px;
		@include cs-scroll; } }
//.home-search end

//.advert
.advert {
	.info-block {
		&__item {
			.icon-delivery {
				width: 50px;
				margin-left: 4px;
				margin-right: 26px; } } } }
//.advert ens

.info-block {

	//.info-block__items
	&__items {
		@include row-flex();
		justify-content: space-between;
		align-items: flex-start;

		@include r-max(md) {
			justify-content: center; }

		@include r-max(xs) {
			// justify-content: flex-start
			margin-right: 0;
			margin-left: 2px; } }

	//.info-block__item
	&__item {
		@include col();
		@include size(4);
		@include size-xs(12);
		@include flexCenter;

		@include r-max(sm) {
			text-align: center;
			flex-direction: column;
			justify-content: center;
			align-items: center; }

		@include r-max(xs) {
			width: auto;
			margin-bottom: 30px;
			text-align-last: left;
			flex-direction: row;
			align-items: flex-start;
			justify-content: flex-start;

			&:last-child {
				margin-bottom: 0; } }

		.icon-delivery {
			margin-right: 26px;
			margin-left: 4px;
			width: 50px;
			@include r(768) {
				width: 70px; }
			@include r(480) {
				width: 55px;
				margin-left: 8px;
				margin-right: 28px; }
			@include r(375) {
				width: 65px;
				margin-left: 3px;
				margin-right: 25px; } } }

	//.info-block__icon
	&__icon {
		width: 65px;
		height: 65px;
		margin-right: 20px;
		fill: $color-text;

		@include r-max(sm) {
			margin-right: 0;
			margin-bottom: 20px; }

		@include r-max(xs) {
			width: 50px;
			margin-bottom: 0;
			margin-right: 20px; }

		&--delivery {
			width: 72px;
			height: 65px; } }

	//.info-block__title
	&__title {
		display: block;
		margin-bottom: 10px;
		color: $color-text;
		font-size: 18px;

		@include r-max(sm) {
			margin-top: auto;
			font-size: 16px; }

		@include r-max(xs) {
			text-align: left; } }

	//.info-block__text
	&__text {
		font-size: 14px;

		@include r-max(xs) {
			text-align: left; } }

	//.info-block__desc
	&__desc {
		max-width: 230px;
		margin-bottom: 0;
		font-family: $f-light;
		line-height: 1.4;

		@include r-max(sm) {
			font-size: 15px; }

		@include r-max(xs) {
			text-align: left; } }

	&--mobile {
		.info-block {
			&__items {
				@include r(480) {
					margin: 0;
					padding: 20px 15px;
					margin-left: 0;
					border-radius: $border-radius;
					background-color: #eee;
					justify-content: flex-start; } }

			&__text {
				span {
					font-family: $f-medium; } }

			&__item {
				@include r(480) {
					width: 100%;
					margin: 0 0 20px 0;
					font-family: $f-light;
					font-size: 14px;
					color: $color-text;
					justify-content: flex-start;
					align-items: center;

					&--link {
						margin-bottom: 0;
						margin-top: 10px;
						font-family: $f;
						color: $color-text--light; } } }

			&__link {
				@include r(480) {
					margin-left: 5px;
					text-decoration: underline;
					color: $color-text--light; } }

			&__icon {
				@include r(480) {
					width: 32px;
					height: 32px;
					&--delivery {
						width: 36px;
						margin-right: 16px; } } } } } }
//.info-block

//.baner
.baner {

	//.bener__bg
	&__bg {
		max-height: 390px;
		height: 100%;
		padding: 30px 60px;
		border-radius: $border-radius;
		overflow: hidden;
		@include cover;

		@include r(768) {
			max-height: 280px;
			padding: 40px; }

		@include r(480) {
			max-height: 200px;
			padding: 20px; } }

	//.bener__title
	&__title {
		margin-bottom: 25px;
		color: #000;
		font-family: $f;
		font-size: 36px;
		@include r(480) {
			font-size: 20px;
			margin-bottom: 10px; } }

	//.bener__desc
	&__desc {
		max-width: 300px;
		margin-bottom: 30px;
		color: #000;
		font-family: $f;
		@include r(480) {
			margin-bottom: 10px; } }

	//.bener__phone
	&__phone {
		color: #000;
		font-family: $f;
		font-size: 36px;
		@include r(480) {
			font-size: 18px; } }

	&--overlay {
		.baner {
			&__bg {
				box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.08);
				position: relative;

				& > * {
					color: #fff;
					position: relative;
					z-index: 1; }

				&::before {
					content: '';
					display: block;
					@include coverdiv;
					background: linear-gradient(170.4deg, #12026D 3.82%, #11022D 35.68%, rgba(84, 0, 113, 0.69) 98.37%);
					z-index: 0; } } } }


	//.baner .slick-slide
	.slick-slide {
		margin: 0; }
	//.baner .slick-dots
	.slick-dots {
		padding: 0 44px;
		bottom: 25px;
		@include r(768) {
			display: none !important; }
		li {
			button {
				&::before {
					width: 10px;
					height: 10px;
					border-radius: 50%; } } } } }
//.baner end


