//.storis
.storis {

	//.storis__list
	&__list {
		margin-right: 10px;
		padding: 10px 0;
		overflow-y: hidden;
		overflow-x: auto;
		@include flexCenter; }

	//.storis__item
	&__item {
		max-width: 120px;
		width: 100%;
		height: 170px;
		margin: 0 5px;
		border-radius: $border-radius;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
		cursor: pointer;
		@include cover;
		@include flexColumn;
		flex-shrink: 0;

		&:first-child {
			margin-left: 12.5px; }

		&:last-child {
			margin-right: 12.5px; }

		&:nth-child(odd) {
			.storis__title {
				color: #fff; } } }

	//.storis__title
	&__title {
		margin: auto 10px 10px;
		font-size: 14px;
		line-height: 1.3; } }
//.storis end

//.storis-slider
.stories-slider {
	background-color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: z(fixed);

	.btn {
		width: 100%;
		margin-top: 50px; }

	.cs-slider {
		height: 100%;
		&__arrow {}
		&__slides {
			height: 100%; }
		&__slide {
			height: 100%;
			padding: 30px 40px;
			@include cover;
			display: flex !important;
			flex-direction: column; }
		&__text {
			margin-top: auto; }
		&__desc {
			margin-bottom: 20px; } }


	.slick-list, .slick-track, .slick-slide {
		height: 100% !important; }

	.slick-slide {
		margin: 0;
		& > div {
			height: 100%; } }

	.slick-dots {
		display: flex;
		justify-content: center;
		bottom: auto;
		top: 5px;

		li {
			width: 100%;

			&.slick-active {
				button {
					&::before {
						height: 2px;
						background: #b1aeae; }

					&::after {
						width: 100%;
						transition: width 5s linear; } } }

			&.is-empty {
				button {
					&::before {
						height: 2px;
						background: #b1aeae; }

					&::after {
						width: 0; } } }

			button {
				width: 100%;
				height: 2px;
				padding: 3px 5px;
				overflow: hidden;

				&::before {
					width: 100%;
					height: 2px;
					border-radius: 35px; }

				&::after {
					content: '';
					display: block;
					width: 100%;
					height: 2px;
					margin-top: -2px;
					border-radius: 35px;
					background-color: #fff;
					overflow: hidden; } } } } }
