@mixin gradient {
	@include gp(to right, $bg-gradient--from, $bg-gradient--to); }

//.list-style
.list-style {
	&--circle {
		li {
			margin-left: 22px;
			list-style-type: disc; } }
	&--def {
		li {
			padding-left: 20px;
			position: relative;
			&::before {
				content: '';
				position: absolute;
				top: 2px;
				left: 0; } } } }
//.list-style end

//.page-wrapper
.page-wrapper {
	min-height: 100vh;
	@include flexColumn;

	&__inner {
		background-color: #fff;
		box-shadow: 0 1px 20px #000;
		position: relative;
		z-index: 2; }

	&.is-auth {
		.login {
			&__clear {
				display: none; }
			&__auth {
				display: block; } } } }
//.page-wrapper end

//.content-wrapper
.content-wrapper {
	@include flexColumn;
	flex: 1; }
//.content-wrapper end

//.cs-section
.cs-section {
	margin-bottom: $section-indent;

	// +r-max(sm)
	// 	margin-bottom: 30px

	//.cs-section__title
	&__title {
		display: block;
		margin-bottom: 30px;
		font-family: $f-medium;
		font-weight: 500;
		font-size: 25px;
		color: #000;
		@include r(1024) {
			font-size: 20px; }
		@include r(768) {
			text-align: center; } }

	&--slider-hero {
		margin-top: -230px;
		@include r-max(sm) {
			margin-top: 0; } }

	&--category {
		@include r-max(sm) {
			order: -1; }
		@include r-max(xs) {
			margin-bottom: 30px; } }

	&--storis, &--advert, &--baner {
		@include r-max(xs) {
			margin-bottom: 30px; }

		.slick-slide {
			.category-item--info {
				margin: 0; } } }

	&--news {
		@include r-max(sm) {
			margin-bottom: $section-indent; } }

	&--product-popular {
		.product-item {
			&__wrap {
				.product-item {
					margin-bottom: 0; } } } } }
//.cs-section end

//.title
.title {
	margin-bottom: 35px;
	font-family: $f-light;
	font-weight: 400;
	font-size: 30px;
	color: $color-text;

	@include r-max(sm) {
		text-align: center; }

	&--low {
		@include r-max(xs) {
			margin-bottom: 20px; } }

	&--slider {
		margin-bottom: 20px; } }
//.title end

//.page-title
.page-title {
	margin-bottom: 40px;
	font-family: $f-light;
	font-weight: 300;
	font-size: 30px;
	color: $color-text; }
//.page-title end

//.section-title
.section-title {
	margin-bottom: 35px;
	color: #000;
	font-family: $f-medium;
	font-size: 36px;
	font-weight: 500; }
//.section-title end

//.badge
.badge {
	min-width: 20px;
	height: 18px;
	padding: 1px 4px 2px;
	text-align: center;
	font-family: $f;
	font-size: 11px;
	line-height: 12px;
	color: #fff;
	background-color: $color-accent;
	border: 2px solid #110159;
	border-radius: 500px;
	overflow: hidden; }
//.badge

.counter {
	//counter__field
	&__field {
		width: 60px;
		padding: 3px 7px;
		text-align: center;
		font-family: $f-medium;
		font-weight: 500;
		color: $color-text;
		font-size: 18px;
		border: 1px solid $gray--lighten;
		border-radius: $border-radius; }
	//counter__control
	&__control {
		padding: 0 10px;
		font-size: 25px;
		line-height: 1;
		font-family: $f-light;
		color: $color-text;
		cursor: pointer;
		outline: none;
		background-color: transparent;
		@include nosel; } }
//.counter

//.list
.list {
	//.list__item
	&__item {
		@include flexCenter;

		& + & {
			margin-top: 20px; }

		&--light {
			font-family: $f-light;
			color: $color-text--light; }

		&--bold {
			font-family: $f-bold; }

		&--medium {
			font-family: $f-medium; } }

	//.list__link
	&__link {}

	//.list__img
	&__img {
		width: 80px;
		margin-right: 30px; }

	//.list__text
	&__text {
		margin-bottom: 0; }

	&--img {

		.list {
			&__link {
				@include flexCenter; } } }

	//.list--dot
	&--dot {
		.list {
			width: 100%;
			&__item {
				& + .list__item {
					margin-top: 10px; }

				position: relative;
				&::after {
					content: '';
					display: block;
					width: 100%;
					height: 3px;
					background-image: url('../img/general/dot.png');
					position: absolute;
					bottom: 6px;
					left: 0; }
				span {
					background-color: #fff;
					position: relative;
					z-index: 2;
					&:first-child {
						padding-right: 5px; }
					&:last-child {
						margin-left: auto;
						padding-left: 5px; } } } } }

	//.list--text
	&--text {
		.list {
			&__item {
				& + .list__item {
					margin-top: 10px; }
				span {
					min-width: 130px; }
				strong {
					font-family: $f-medium;
					font-weight: 500; }
				a {
					color: $color-text; } } } }

	//.list--light
	&--light {
		.list {
			&__item {
				font-size: 14px;
				color: $color-text--light;
				& + .list__item {
					margin-top: 5px; }
				span {
					min-width: 130px; }
				strong {
					font-family: $f-medium;
					font-weight: 500;
					color: $color-text; }
				a {
					color: $color-text; } } } } }
//.list end

//.item-icon
.item-icon {
	//.item-icon__wrap
	&__wrap {
		@include row-flex;

		.item-icon {
			@include col;
			@include size(4);
			@include size-xs(12);
			@include flexColumn;
			align-items: center;
			justify-content: center;
			text-align: center;

			@include r(480) {
				margin-bottom: 15px;
				display: flex;
				flex-direction: row;
				justify-content: flex-start; } } }

	//.item-icon__ico
	&__ico {
		width: 32px;
		height: 32px;
		margin-bottom: 10px;
		@include r(480) {
			margin-bottom: 0;
			margin-right: 10px; } }

	//.item-icon__text
	&__text {
		font-size: 14px;
		color: $color-text--light; } }

//.privacy
.privacy {
	font-size: 11px;
	text-align-last: auto;
	a {
		color: $color-accent; } }
//.privacy end

//.policy-agriment
.policy-agriment {
	text-align: left;
	font-family: $f-light;
	font-size: 13px;
	color: $color-text;

	a {
		color: $color-text;
		text-decoration: underline;
		&:hover {
			text-decoration: none; } }

	.cs-checkbox__title {
		font-size: 13px;
		color: $color-text; } }
//.policy-agriment end
