.modal-window {
	padding: 30px 50px;
	border-radius: $border-radius;

	//modal-window__wrap
	&__wrap {}

	//modal-window---medium
	&--medium {
		max-width: 768px;
		width: 100%;
		@extend %responsMobile; }

	//modal-window--offset
	&--offset {
		padding: 40px 70px; }

	//modal-window--mobile
	&--mobile {
		max-width: 460px;
		width: 100%;
		@extend %responsMobile; }

	//modal-window__header
	&__header {
		text-align: center;
		margin-bottom: 25px;

		&.text-left {
			text-align: left; } }

	//modal-window__title
	&__title {
		margin-bottom: 0;
		font-family: $f;
		font-weight: 400;
		font-size: 20px; }

	.fancybox-close-small {
		padding: 0;
		width: 30px;
		height: 30px;
		top: 10px;
		right: 10px;
		svg {
			width: 30px;
			opacity: 0.5;
			&:hover {
				opacity: 1; } } }

	.info-text {
		margin-top: 20px; } }
//modal-window

.info-text {
	color: #000;
	font-size: 13px;
	line-height: 1.2; }

.review-add {
	.pedit__file {
		margin-left: 0; }
	.star-rating {
		&__ico {
			width: 20%;
			height: 38px;
			border: 1px solid $gray--light;
			border-left: 0;
			@include flexCenter;
			justify-content: center;
			&::before {
				margin: auto; }
			&[for='star-rating-1'] {
				border-left: 1px solid $gray--light;
				border-radius: $border-radius 0 0 $border-radius; }
			&[for='star-rating-5'] {
				border-radius: 0 $border-radius $border-radius 0; } } } }
//.review-add

//.new-profile
.new-profile {
	padding: 50px 70px;
	display: flex;
	justify-content: center;

	@include r(768) {
		max-width: 414px; }

	@include r(480) {
		padding: 15px 20px 25px 20px; }

	&__title {
		margin-bottom: 40px;
		font-size: 18px;

		@include r(480) {
			font-family: $f-medium;
			font-weight: 500;
			font-size: 16px; } }

	.button-box {
		@include r(768) {
			flex-direction: column; }

		.btn {
			min-width: 200px;
			padding: 20px 35px;
			outline: none;
			box-shadow: $bxs-box;

			@include r(480) {
				min-width: 100%;
				padding: 25px 35px; }


			&:hover {
				box-shadow: 0 5px 25px rgba(0,0,0,.15); }

			& + .btn {
				margin-left: 30px;

				@include r(768) {
					margin-left: 0;
					margin-top: 20px; } } } } }

.enter {
	padding: 30px 70px 50px;
	@include r(480) {
		padding: 30px 20px; }

	.form {
		margin-bottom: 30px;
		@include r(480) {
			margin-bottom: 10px; }
		&__inner {
			@include row-flex;
			align-items: flex-end;
			@include r(480) {
				flex-direction: column; } }
		&__box {
			@include col;
			@include size(4);
			@include size-xs(12);
			margin-bottom: 0;
			@include r(480) {
				margin-bottom: 20px; } }
		&__input {
			@include r(480) {
				width: 100%; } }
		.btn.w100 {
			@include r(480) {
				margin-top: 10px; } } }

	.button-box {
		margin-bottom: 50px;

		.form__link {
			font-family: $f;
			color: #13599a;
			& + .form__link {
				margin-left: 20px; } } }

	.fancybox-close-small {
		top: 30px;
		right: 15px; }

	.modal-window {
		&__header {
			margin-bottom: 40px;
			text-align: left;
			@include r(480) {
				margin-bottom: 20px; } } } }

//.modal-map
.modal-map {
	max-width: 1100px;
	width: 100%;
	padding: 0;
	@include r(1200) {
		max-width: 768px; }
	@include r(768) {
		max-width: 758px; }
	@include r(480) {
		max-width: 396px; }
	@include r(375) {
		max-width: 355px; }
	@include r(320) {
		max-width: 300px; }

	.map {
		&__left {
			@include r(480) {
				min-height: 400px;
				width: 100%; } }
		&__desc {
			margin-top: auto;
			padding: 15px 0 0;
			background-color: #fff;
			// position: absolute
			// bottom: 0
			// left: 0
			// width: 100%
			@include r(768) {
				border-top: 1px solid $color-border--lighten; } } }

	.map-address {
		overflow-y: auto;

