//.nav
.nav {
	//.nav__list
	&__list {
		@include flexCenter;
		flex: 1; }

	//.nav__item
	&__item {
		margin-right: 15px;
		position: relative;

		&--last {
			margin-left: auto; }

		&.is-active {
			.nav__link {
				&::after {
					width: 100%; } } }

		&.cs-dropdown {
			border-radius: $border-radius $border-radius 0 0;
			&:hover {
				.nav__link {
					color: #000;
					background-color: #fff;
					&::before {
						content: '\f106'; } } }
			.nav__link {
				padding: 5px 25px 5px 10px;
				line-height: 1.7;
				transition: initial;
				&::after {
					display: none; }
				&::before {
					content: '\f107';
					@include fa('light');
					display: inline-block;
					@include vy;
					right: 10px; } } }

		.cs-dropdown {
			&__link {
				&::after {
					@include rmin(481) {
						display: none; } } } } }

	//.nav__link
	&__link {
		display: block;
		padding: 0 10px;
		font-size: 12px;
		line-height: 41px;
		border-radius: $border-radius;
		color: #fff;
		position: relative;

		// &::after
		// 	content: ''
		// 	display: block
		// 	height: 4px
		// 	width: 0
		// 	background-color: $color-accent
		// 	+centre('x')
		// 	bottom: -10px
		// 	height: 2px
		// 	+tr

		&:hover {
			color: $color-accent;

			// &::after
 } }			// 	width: 100%

	//.nav--main
	&--main {
		@include r(992) {
			display: none; }
		.nav {
			&__item {
				&--auth {
					display: none;
					min-width: 170px;
					width: 100%;
					.nav__link {
						@include text-overflow; } } } } } }
//.nav end

//.mobile-nav
.mobile-nav {
	padding-top: 70px;
	padding-bottom: 70px;
	background-color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 199;
	transform: translateX(-100vw);
	@include tr;
	overflow-y: auto;
	overflow-x: hidden;
	@include smooth-scroll;

	@include r-min(xs) {
		max-width: 375px;
		box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.25); }

	&.is-open {
		transform: translateX(0);
		z-index: z(menu); }

	//.mobile-menu__list
	&__list {
		margin: 0 30px 15px 50px;
		padding-bottom: 15px;
		border-bottom: 1px solid $color-border--lighten; }

	//.mobile-menu__item
	&__item {

		&::before {
			top: 20px; }

		&--auth {
			display: none; } }

	//.mobile-menu__link
	&__link {
		display: block;
		padding: 10px 0 10px 10px;
		font-size: 14px;
		color: $color-text;
		position: relative;
		cursor: pointer;
		// -webkit-tap-highlight-color:  rgba(255, 255, 255, 0)

		@include r(480) {
			font-size: 16px; }

		&:hover, &:focus {
			color: #000; }

