 }		// 	display: none

	//.mobile-menu__search
	&__search {
		position: fixed;
		right: 22px;
		top: 21px;
		z-index: z(dropdown); }

	//.mobile-menu__close
	&__close {
		width: 40px;
		height: 40px;
		padding: 10px;
		cursor: pointer;
		fill: $color-text--light;
		@include tr;
		position: absolute;
		top: 10px;
		left: 10px;
		&:hover {
			fill: $color-text; } }
	//.mobile-nav--sub
	&--sub {
		padding-top: 70px;
		padding-left: 50px;
		padding-right: 30px;
		background-color: #fff;
		@include coverdiv;
		z-index: z(fixed);
		transform: translateX(200%);
		transition: transform 0.35s linear;
		will-change: transform;
		&.is-open {
			transform: translateX(0); } }

	.search {
		&__submit {
			@include s(search--mobile); }
		&__field {
			color: #000; } }

	.search-hint {
		border: 1px solid #bbb;
		border-top-color: transparent;
		background-color: #fff;
		&__item {
			& + .search-hint__item {
				border-top-color: #bbb; } }
		&__link {
			color: $color-text; } }

	.cs-dropdown {
		&.is-active {
			&::before {
				content: '\f107'; } }

		&::before {
			content: '\f105';
			@include fa('light');
			right: 0; }

		&__list {
			margin-left: 30px;
			position: static;
			border-top: 0;
			box-shadow: none; }

		&__link {
			padding-left: 25px; } }

	.phone {
		margin-top: 30px;
		margin-bottom: 20px;
		@include flexCenter;
		&__link {
			margin: auto;
			color: $color-text; } }

	.social {
		&__list {
			margin: auto; } } }
//.mobile-nav end

//.hamburger
.hamburger {
	line-height: 1;
	border: 0;
	background-color: transparent;
	outline: none;
	&__icon {
		width: 20px;
		height: 20px;
		fill: #fff; } }
//.hamburger end

//.toggle
.toggle {
	display: block;
	width: 23px;
	height: 17px;
	margin-top: 2px;
	pointer-events: none;

	span {
		position: relative;
		display: block;
		width: 100%;
		height: 2px;
		background-color: #fff;
		transition: all 0.15s;
		backface-visibility: hidden;
		border-radius: 2px;

		&:after, &:before {
			content: ' ';
			position: absolute;
			left: 0;
			top: 7px; }

		&:after {
			top: 14px;
			width: 100%;
			height: 2px;
			background-color: #fff;
			transition: all 0.3s;
			backface-visibility: hidden;
			border-radius: 2px; }

		&:before {
			width: 100%;
			height: 2px;
			background-color: #fff;
			transition: all 0.3s;
			backface-visibility: hidden;
			border-radius: 2px; } }

	&.is-active {
		pointer-events: auto;
		span {
			background-color: transparent;
			&:before {
				transform: rotate(45deg) translate(-2px, 2px); }
			&:after {
				transform: rotate(-45deg) translate(3px, -7px); } } } }
//.toggle end
