//.card
.card {
	background-color: $color-bg;
	position: relative;
	@include flexColumn;

	@include r(768) {
		background-color: #fff; }

	@include r(480) {
		padding-top: 20px; }

	//.card__top-line
	&__top-line {
		@include flexCenter;
		@include r(480) {
			flex-wrap: wrap;
			margin-bottom: 0; }
		.back-to-search {
			margin-left: auto;
			@include r(480) {
				display: none; } } }

	//.card__inner
	&__inner {
		margin-bottom: 70px;
		@extend %box;
		@include flexCenter;

		@include r(768) {
			margin-bottom: 10px;
			border: 0;
			box-shadow: none; }

		@include r(480) {
			margin-bottom: 10px;
			flex-direction: column; }

		&--center {
			align-items: center;
			@include r(1024) {
				flex-direction: column;
				.card__left {
					order: 2; }
				.card__right {
					order: 1;
					width: 100%; } } } }

	//.card__img
	&__img {
		width: calc(40% - 15px);
		margin-right: 30px;
		border-radius: $border-radius 0 0 $border-radius;
		overflow: hidden;
		flex-shrink: 0;
		@include flex;

		@include r(768) {
 }			// margin-top: 0

		@include r(480) {
			width: calc(30% - 15px);
			width: 100%;
			margin-right: 0;
			margin-bottom: 20px; }

		@include r(375) {
			margin-bottom: 20px; }

		&--no-border {
			border: 0; }

		.cs-slider {
			margin: auto;
			width: 100%;

			&__arrow {
				//.card__img .cs-slider__arrow--next
				&--next {
					right: 15px; }
				//.card__img .cs-slider__arrow--prev
				&--prev {
					left: 15px; } } } }

	//.card__desc
	&__desc {
		padding: 40px 45px 60px 0;
		flex-grow: 1;

		@include r(768) {
			padding: 20px 20px 20px 0; }

		@include r(480) {
			width: 100%;
			padding-top: 0;
			padding-right: 0; }

		//.card__desc .button-box
		.button-box {
			margin-bottom: 50px;

			@include r(480) {
				width: 100%;
				margin-bottom: 0;
				flex-direction: column;
				justify-content: center; } } }

	//.card__info
	&__info {
		margin-bottom: 20px;
		@include r(768) {
			margin-bottom: 0; }
		.price__stock {
			display: block;
			margin-bottom: 20px; }
		.cs-accordeon {
			&__title {
				display: inline-block;
				margin-bottom: 20px;
				border-bottom: 1px dashed $color-border--light;
				cursor: pointer;
				@include rmin(769) {
					display: none; } }
			&__content {
				@include r(768) {
					display: none; } } } }

	//..card__shops
	&__shops {
		font-family: $f-light;
		font-size: 14px;
		color: $color-text--light;
		@include flexCenter;

		@include r(768) {
			flex-direction: column;
			align-items: flex-start; }

		@include r(480) {
			margin-top: 10px;
			flex-direction: row;
			align-items: center;

			.btn {
				font-size: 14px;
				text-decoration: underline; } }

		.btn {
			font-family: $f;
			font-weight: 400;
			color: $color-text--light;
			@include r(768) {
				margin-left: 0; }
			@include r(480) {
				margin-left: 7px; } } }

	//.card__product-info
	&__product-info {
		display: inline-block;
		margin-left: auto;
		font-size: 14px;

		width: calc(50% - 15px);

		@include r(768) {
			width: 100%; }

		.button-box {
			margin-bottom: 30px;
			@include flexCenter;
			@include r(480) {
				flex-direction: column; } }
		.button-link {
			color: $color-text;
			border-color: $color-text; }
		.price__stock {
			display: block;
			@include r(768) {
				margin-bottom: $gutter; } } }

	//.card__left
	&__left {
		margin-right: 30px;
		flex: 1;
		@include r(768) {
			margin-right: 0; }
		.product-item {
			@include r(768) {
				.price__cost {
					font-size: 20px; } }
			&__wrap {
				margin-bottom: 0;
				@include r(1024) {
					flex-wrap: nowrap; }
				@include r(480) {
					flex-wrap: wrap; } } } }

	//.card__right
	&__right {
		width: 30%; }

	//.card__title
	&__title {
		margin-bottom: 30px;
		font-family: $f-medium;
		font-weight: 500;
		font-size: 22px;
		@include nosel;

		@include r(768) {
			margin-bottom: 20px;
			font-size: 20px; }

		@include r(480) {
			margin-bottom: 15px;
			font-family: $f;
			font-weight: 400; } }

	//.card__subtitle
	&__subtitle {
		margin-bottom: 15px;
		font-family: $f-medium;
		font-weight: 500;
		font-size: 20px; }

	//.card__more-info
	&__more-info {
		margin-bottom: 20px;
		font-size: 14px;
		color: $color-text--light;
		cursor: pointer;

		&:hover {
			color: $color-text;
			.icon {
				fill: $color-text; } }
		i {
			margin-right: 7px; }

		.icon {
			margin-right: 7px;
			fill: $color-text--light;
			position: relative;
			top: 5px; } }

	//.card__sum
	&__sum {
		margin-bottom: 40px;
		@include flexCenter;

		@include r(768) {
			margin-bottom: 30px; }

		@include r(480) {
			margin-bottom: 25px; } }

	//.card__price
	&__price {
		.price {
			//.card__price .price__cost
			&__cost {
				font-family: $f-bold;
				font-size: 36px;
				color: $color-text;
				@include r(768) {
					font-family: $f-medium;
					font-size: 26px; } } } }

	//.card__counter
	&__counter {
		margin-left: 30px;
		@include r(768) {
			margin-left: auto; }
		@include r(480) {
			margin-left: auto; } }

	//.card__rating
	&__rating {
		margin-left: 30px; }

	//.card__btn
	&__btn {
		&:first-child {
			margin-right: 30px;
			@include r(768) {
				padding: 14px 20px; } }

		@include r(480) {
			width: 100%;
			&:first-child {
				margin-right: 0;
				margin-bottom: 10px; } } }

	//.card__info-block
	&__info-block {
		@include r(1024) {
			margin-top: 30px; }
		.info-block {
			&__items {
				margin-top: 60px;
				flex-direction: column;
				@include r(1024) {
					margin-top: 0;
					flex-direction: row; } }
			&__item {
				margin-bottom: 15px;
				@include r(480) {
					margin-bottom: 15px; }
				&:last-child {
					margin-bottom: 0;
					@include r(480) {
						margin-bottom: 15px; } }
				@include size(12);
				//. +size-md(4)
				@include size-sm(6);
				@include size-xs(12);
				@include r(768) {
					margin-bottom: 40px;
					justify-content: center; }
				@include r(480) {
					justify-content: flex-start;
					margin-left: 35px; } } } }

	//.card__recomended
	&__recomended {
		margin-bottom: $gutter*2;
		.product-item {
			width: 25%; } }

	//.card__card-info
	&__card-info {
		@include r(768) {
			display: none; } }

	//.cart__slider
	&__slider {
		height: 100%;
		margin: auto;
		.cs-slider {
			&__slides {
				padding-top: 17%;
				@include r(768) {
					padding-top: 0; } } }
		.slick-dots {
			bottom: -65px;
			left: 0;
			@include flex;
			@include r(1024) {
				bottom: -126px; }
			@include r(768) {
				bottom: 2px; }
			li {
				width: 100%;
				button {
					padding: 0;
					width: 100%;
					height: 3px;
					&::before, &::after {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 3px; }
					&::after {
						content: '';
						display: block;
						width: 0;
						background-color: #686868;
						z-index: 10; } }
				&.slick-active {
					button {
						&::before {
							background-color: #b1aeae; }
						&::after {
							width: 100%;
							transition: width 2s linear; } } } } } }

	.product-item {
		$o: 10px;

		@include size(3);
		@include size-sm(4);
		@include size-xs(6);
		@include size-i5(12);

		@include r(768) {
			&:last-child {
				display: none; } }

		@include r(480) {
			&__wrap {
				@include u-row-flex($o);

				.product-item {
					margin-bottom: $o;
					padding: 0 15px 15px;
					@include u-col($o);
					@include u-size(6, 12, $o);

					&:last-child {
						display: block; }

					&__title {
						margin-bottom: 15px;
						font-size: 12px; }

					&__btn {
						width: 50px;
						height: 50px;

						.icon {
							width: 22px;
							height: 22px; } } } } }

		@include r(320) {
			&__wrap {
				.product-item {
					margin: 0 auto $o;
					@include u-size(12, 12, $o);
					&__title {
						font-size: 14px; } } } } }

	.product-info {
		//.card .product-info__list
		&__list {
			columns: 2 auto;
			@include r(480) {
				columns: auto; }
			&.w3 {
				columns: 3 auto;
				@include r(480) {
					columns: auto; } } }

		&__item {
			color: $color-text--light;
			span {
				color: $color-text; } } }

	.info-block {
		&__items {
			@include r(480) {
				margin: 0;
				padding: 20px 15px;
				margin-left: 0;
				border-radius: $border-radius;
				background-color: #eee;
				justify-content: flex-start; } }

		&__item {
			justify-content: center;
			@include r(480) {
				width: 100%;
				margin: 0 0 20px 0;
				font-family: $f-light;
				font-size: 14px;
				color: $color-text;
				justify-content: flex-start;
				align-items: center;

				&--link {
					margin-bottom: 0;
					margin-top: 10px;
					font-family: $f;
					color: $color-text--light; } } }


		&__link {
			@include r(480) {
				margin-left: 5px;
				text-decoration: underline;
				color: $color-text--light; } }

		&__text {
			span {
				font-family: $f-medium; } }

		&__icon {
			width: 45px;
			@include r(768) {
				width: 50px;
				&--delivery {
					width: 58px; } }
			@include r(480) {
				width: 32px;
				height: 32px;
				&--delivery {
					width: 36px;
					margin-right: 16px; } } } }

	.title {
		@include r(768) {
			text-align: left; }
		@include r(480) {
			font-size: 20px; } }

	.title--low {
		@include r(768) {
			margin-bottom: 15px; }
		@include r(480) {
			margin-bottom: 20px;
			font-size: 23px; } }

	.cs-slider--card {
		.slick-dots {
			bottom: 0; } }

	.cs-section {
		@include r(480) {
			margin-bottom: 50px; }

		&:last-child {
			margin-bottom: 30px; }

		&--advert {
			@include r(768) {
				order: 2; }
			@include r(480) {
				order: initial;
				margin-bottom: 60px; } }

		&--similar {}

		&--about {
			p {
				margin-bottom: 35px;
				color: $color-text--light; }

			.card__more-info {
				font-family: $f-medium;
				font-weight: 500;
				font-size: 18px;
				color: $color-text; } } }

	.container {
		&.p-20 {
			@include r(480) {
				padding: 0 20px; } } } }
//.card end

//.card-info
.card-info {
	@include r(768) {
		margin-bottom: 20px; }

	@include r(480) {
		padding: 0 20px; }

	//.card-info__item
	&__item {
		margin-bottom: 30px; }

	//.card-info__title
	&__title {
		margin-bottom: 15px;
		font-family: $f-medium;
		font-weight: 500;
		font-size: 20px; }

	//.card-info__content
	&__content {
		p {
			color: $color-text--light;
			&:last-child {
				margin-bottom: 0; } }

		.product-info {
			&__item {
				font-size: 15px; } } }

	//.card-info__back
	&__back {
		display: inline-block;
		margin-bottom: 10px;
		padding-right: 30px;
		i {
			font-size: 25px;
			color: $color-text--light; } }

	//.card-info__link
	&__link {
		padding: 0;
		color: #000;
		border-bottom: 1px dashed #000;
		@include r(768) {
			margin: 0 auto 15px; } }

	//.card-info__empty
	&__empty {
		@include flexCenter;
		@include r(768) {
			flex-wrap: wrap;
			text-align: center; }
		p {
			margin-bottom: 0;
			flex: 1;
			@include r(768) {
				width: 100%;
				margin-bottom: 30px;
				flex: auto; } } }

	//.card-info__toggle
	&__toggle {
		position: absolute;
		right: 0;
		bottom: 0; }

	&--mobile {
		display: none;
		padding: 30px; }

	.tab {
		&__wrap {
			width: 100%;
			margin: 0;
			@include r(768) {
				border: 0; } }

		//.card-info .tab__titles
		&__titles {
			justify-content: flex-start;
			@include r(768) {
				justify-content: center; }
			@include r(480) {
				flex-direction: column;
				justify-content: flex-start; } }

		//.card-info .tab__title
		&__title {
			width: auto;
			padding: 0;
			font-size: 14px;
			color: $color-text--light;
			background-color: transparent;
			border-bottom: 2px solid transparent;
			justify-content: flex-start;
			cursor: pointer;

			& + .tab__title {
				margin-left: 50px;
				@include r(768) {
					margin-left: 0; } }

			@include r(768) {
				margin: auto;
				border-bottom: 0;
				height: auto;
				justify-content: center;

				&:first-child {
					margin-left: 0; }

				&:last-child {
					margin-right: 0; } }

			@include r(480) {
				width: 100%;
				margin-bottom: 15px;
				font-size: 14px;
				text-align: left;
				flex-direction: column;
				justify-content: flex-start;
				align-items: flex-start;
				&:last-child {
					margin-right: auto; } }

			&:hover {
				color: #000; }

			&.is-active {
				span {
					color: $color-text;
					border-bottom: 2px solid $color-text; }
				@include r(768) {
					border-bottom: 0; } }

			//.card-info__title a
			a {
				@include r(480) {
					text-decoration: underline; } } }

		//.card-info .tab__content
		&__content {
			min-width: 100%;
			min-height: 100px;
			padding: 25px 0 0;
			color: $color-text--light;
			font-size: 14px;
			position: relative;

			@include r(768) {
				min-height: auto;
				padding: 20px 0; }

			@include r(480) {
				display: block;
				padding: 10px 0 0; }

			p {
				font-size: 14px; } }

		&--transform {
			@include r(768) {
				.cs-accordeon__item {
					margin-bottom: 0;
					border-top: 1px solid $gray--lighten;
					&:last-child {
						border-bottom: 1px solid $gray--lighten; }
					&.is-open {
						.cs-accordeon__title {
							background-color: $gray--lighten; } } } } } } }
//.card-info end

//.sertificate
.sertificate {
	&__items {
		@include flexCenter;
		@include r(768) {
			text-align: center;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap; } }

	&__item {
		max-height: 305px;
		width: 20%;
		margin-left: 20px;
		&:first-child {
			flex: 1;
			margin-left: 0;
			@include r(768) {
				width: 100%;
				margin-bottom: 20px;
				text-align: center;
				flex: auto; } } }

	&__download {
		@include flexCenter;
		@include r(768) {
			justify-content: flex-start; }

		.icon {
			width: 60px;
			height: 60px;
			margin-right: 20px; }
		a {
			color: #000;
			&:hover {
				span {
					border-bottom-color: transparent; } } }

		span {
			display: block;
			margin-bottom: 10px;
			border-bottom: 1px dashed #000; } } }
//.sertificate end

//.reviers
.reviers {

	//.reviers__items
	&__items {
		@include r(480) {
			display: flex;
			flex-direction: column; }
		.reviers__btn {
			margin-left: 183px;
			@include r(480) {
				margin: 0 auto 15px; } } }

	//.reviers__item
	&__item {
		margin-bottom: $gutter; }

	//.reviers__inner
	&__inner {
		@include flex;
		@include r(480) {
			flex-direction: column; } }

	//.reviers__user
	&__user {
		width: 153px;
		margin-right: 30px;
		@include r(480) {
			margin-bottom: 5px;
			width: 100%;
			display: flex;
			justify-content: space-bettwen;
			flex-wrap: wrap;
			strong, span {
				margin: auto; } }
		strong {
			display: block;
			margin-bottom: 5px;
			@include r(480) {
				margin-left: 0; } }
		span {
			display: block;
			margin-bottom: 3px;
			color: $color-text; } }

	//.reviers__text
	&__text {
		width: calc(100% - 153px - 30px);
		@include r(480) {
			width: 100%; } }

	//.reviers__rating
	&__rating {
		margin-top: 5px;
		margin-bottom: 15px;
		.star-rating__ico {
			margin-right: 3px; } }

	//.reviers__desc
	&__desc {
		display: block;
		margin-bottom: 10px; }

	//.reviers__list
	&__list {
		@include flexCenter;
		li {
			width: 60px;
			height: 60px;
			border: 1px solid $color-border--lightest;
			border-radius: $border-radius;
			@include flexCenter;
			& + li {
				margin-left: 15px; } } }

	//.reviers__btn
	&__btn {
		@include r(768) {
			margin: auto; } }
	//.reviers__empty
	&__empty {
		@include flexCenter;
		@include r(768) {
			flex-wrap: wrap;
			text-align: center; }
		p {
			margin-bottom: 0;
			flex: 1;
			@include r(768) {
				width: 100%;
				margin-bottom: 30px;
				flex: auto; } } } }
//.reviers end
