.delivery {
	padding-bottom: $section-indent;
	background-color: $color-bg;

	@include r-max(md) {
		padding-bottom: 30px;
		background-color: #fff; }

	//.delivery__title
	&__title {
		margin-bottom: 30px;

		@include r-max(xs) {
			margin-bottom: 15px; } }

	//.delivery__desc
	&__desc {}

	//.delivery__subtitle
	&__subtitle {}

	//.delivery__items
	&__items {}

	//.delivery__form
	&__form {
		width: 100%;
		@include r-max(xs) {
			margin-top: 30px; }

		.h4 {
			margin-bottom: 20px; }

		.button-box {
			justify-content: flex-end; } }

	.cs-card {
		@include r-max(md) {
			margin-bottom: 30px;
			padding: 0;
			background-color: transparent;
			box-shadow: none;

			&__inner {
				margin-bottom: 0; }

			&__text {
				&:last-child {
					margin-bottom: 0; } } }

		&__text {
			color: $color-text--light;
			@include r-max(xs) {
				font-size: 14px; } }

		&__map {
			@include r(480) {
				margin-top: 0; } }

		&--reverse {
			.cs-card {
				&__img {
					margin-bottom: 30px; } } }

		&:not(.cs-card--transform) {
			@include r-max(xs) {
				margin-bottom: 50px;
				padding: 0; }

			.cs-card__title {
				font-family: $f-light;
				font-weight: 300;
				font-size: 20px;

				@include r-max(md) {
					margin-bottom: 15px;
					font-size: 20px;
					font-family: $f-medium;
					font-weight: 500; }

				@include r-max(xs) {
					font-size: 18px; } } }

		.page-title {
			@include r-max(md) {
				margin-bottom: 20px;
				font-family: $f-medium;
				font-weight: 500;
				font-size: 20px; }

			@include r-max(xs) {
				margin-bottom: 10px;
				font-size: 18px; } } }

	.map {
		border-radius: 0 $border-radius $border-radius;
		overflow: hidden;
		@include r-max(xs) {
			margin-bottom: 20px; } } }
//.delivery
