//.auth
.auth {
	padding-bottom: $section-indent;
	background-color: $color-bg;
	@include r(480) {
		background-color: #fff; }

	&__inner {
		width: 100%;
		@include row-flex;
		align-items: center;
		@include r(480) {
			margin: auto;
			text-align: center; }

		&--start {
			align-items: flex-start; } }

	&__left {
		@include col;
		@include size(5);
		@include size-md(6);
		@include size-xs(12); }

	&__right {
		@include col;
		@include size(7);
		@include size-md(6);
		@include r(480) {
			display: none; } }

	//.auth .form
	.form {
		max-width: 320px;
		@include r(480) {
			max-width: 100%; }
		&__link {
			font-family: $f;
			color: #13599a; } }

	.cs-card {
		@include r(480) {
			box-shadow: none;
			border: 0; } }

	.page-title {
		@include r(1024) {
			font-size: 32px; }
		@include r(768) {
			font-size: 27px; }
		@include r(480) {
			font-size: 22px; }
		&--big {
			margin-bottom: 70px;
			@include r(1024) {
				margin-bottom: 30px; } } } }
