/* Dots */

/*// .slick-slider {
//     margin-bottom: 30px;
// }*/

.slick-dots {
    position: absolute;
    bottom: 22px;
    list-style: none;
    display: block;
    padding: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        cursor: pointer;
        button {
            display: block;
            padding: 3px;
            border: 0;
            background: none;
            outline: none;
            line-height: 0;
            font-size: 0;
            color: red;
            cursor: pointer;
            &:before {
                content: "";
                display: block;
                height: 5px;
                width: 40px;
                box-sizing: border-box;
                background: #b1aeae;
                transition: all 0.1s ease;
            }
            &:hover {
                &:before {
                   background: #686868;
                }
            }
        }
        &.slick-active button:before {
            background: #686868;
        }
    }
}
