//.privacy
.privacy {
	font-family: $f-light;
	background-color: $color-bg;

	@include r(1024) {
		background-color: #fff; }

	@include r(480) {
		padding: 30px 0; }

	&__in {
		max-width: 730px;
		margin: 60px auto 0;

		.privacy__subtitle {
			margin-bottom: 10px;
			@include r(480) {
				margin-bottom: 5px;
				text-align: left; }
			& + p {
				font-family: $f-light;
				@include r(480) {
					text-align: left;
					font-size: 14px; } } }

		.pedit {
			max-width: 650px;
			margin: auto;

			&__item {
				&--text {
					margin-bottom: 10px; } } } }

	&__wrap {
		margin-bottom: $gutter*2;
		padding: 45px 85px;
		@extend %box;
		border-radius: $border-radius;

		@include r(1024) {
			margin: 0 0 30px;
			padding: 0;
			border: 0;
			border-radius: 0;
			box-shadow: none; } }

	&__inner {
		display: flex;
		align-items: flex-start;
		@include r(480) {
			flex-wrap: wrap; } }

	&__left {
		width: 50%;
		margin-right: 60px;

		@include r(768) {
			margin-right: 30px; }

		@include r(480) {
			width: 100%;
			margin-right: 0;
			margin-bottom: 30px; } }

	&__right {
		width: 50%;
		margin-left: 60px;
		@include r(768) {
			margin-left: 30px; }
		@include r(480) {
			width: 100%;
			margin-left: 0; } }

	//.privacy__title
	&__title {
		display: block;
		margin-bottom: 30px;
		font-size: 30px;
		font-family: $f;
		font-weight: 400;

		@include r(1024) {
			font-family: $f-medium;
			font-weight: 500;
			font-size: 20px; } }

	//.privacy__subtitle
	&__subtitle {
		display: block;
		margin-bottom: 30px;
		font-size: 22px;
		line-height: 1.5;
		font-family: $f-light;
		font-weight: 300;

		@include r(1024) {
			font-size: 18px; }

		@include r-max(sm) {
			font-size: 20px; }

		@include r(480) {
			margin-bottom: 15px; }

		@include r(375) {
			font-size: 18px; }

		&:not(:first-child) {
			margin-top: 60px;
			@include r(480) {
				margin-top: 40px; } } }

	//.privacy__form
	&__form {
		.button-box {
			display: flex;
			justify-content: flex-end; } }

	.page-title {
		margin-bottom: 20px;

		@include r(1024) {
			font-family: $f-medium;
			font-weight: 500;
			font-size: 20px; } }

	p {
		font-family: $f-light; }

	span {
		display: block;
		margin-bottom: 5px;
		font-size: 15px; }

	ul:not(.breadcrumbs__list) {
		margin-bottom: 15px;
		li {
			margin-bottom: 15px;
			font-size: 15px; } } }
//.privacy end

//.garanty
.garanty {
	background-color: $color-bg;

	@include r-max(md) {
		background-color: #fff; }

	@include r(480) {
		padding: 30px 0; }

	p {
		font-family: $f-light; }

	strong {
		font-family: $f-medium;
		font-weight: 500; }

	.page-title {
		margin-bottom: 20px;

		@include r(1024) {
			font-family: $f-medium;
			font-weight: 500;
			font-size: 20px; } }

	ul:not(.breadcrumbs__list) {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0; }
		li {
			margin-bottom: 10px;
			font-size: 15px;
			font-family: $f-light;
			font-weight: 300;
			&:last-child {
				margin-bottom: 0; } } } }
//.garanty end
