//.firstscreen
.firstscreen {
	background-image: $gradient;
	position: relative;

	@include r-max(sm) {
		min-height: auto;
		.search__wrap {
				margin-bottom: 30px; } }

	@include r-max(xs) {
		min-height: 100%; }

	//.firstscreen__title
	&__title {
		margin-top: 40px;
		margin-bottom: 50px;
		font-family: $f-light;
		font-size: 30px;
		font-weight: 300;
		line-height: 1.3;
		text-align: center;
		color: #fff;

		@include r-max(sm) {
			margin: 30px 0 40px; } }

	//.firstscreen__line
	&__line {
		margin: 40px 0;
		@include flexCenter;

		@include r-max(sm) {
			margin: 20px 0 0;
			flex-wrap: wrap;
			flex-direction: column;

			.c-select__wrap {
				margin-bottom: 20px; } } }

	//.firstscreen__c-select
	&__c-select {
		margin-right: $gutter;
		color: $color-secondary;
		box-shadow: $bxs-header;

		@include r-max(sm) {
			margin-right: 35px;
			color: #fff;
			box-shadow: none;
			background-color: transparent; }

		@include r-max(xs) {
			margin-right: 15px; }

		@include r-max(i5) {
			margin-right: 0; }

		.toggle {
			span {
				background-color: $color-secondary;
				@include r-max(sm) {
					background-color: #fff; }

				&::before , &::after {
					background-color: $color-secondary;
					@include r-max(sm) {
						background-color: #fff; } } }
			&.is-active {
				span {
					background-color: transparent; } } } }

	.search {
		width: 100%;
		flex: 1;
		@include r(480) {
			width: 92%;
			margin: auto; }

		&__wrap {
			@include r-max(sm) {
				width: 100%;
				margin-bottom: 20px;
				order: -1;
				flex: 1; } } } }

